<template>
  <div ref="player" id="youtube-player-vue"></div>
</template>

<script>
import YouTubePlayer from 'youtube-player';

export default {
    name : "YoutubeVue3",
    props: {
        width: { type:Number, default:480 },
        height: { type:Number, default:320 },
        autoplay: { type:Number, default:0, validator: (v) => Number(v)===0||Number(v)===1 },
        videoid : { type:String, required:true },
        hl: { type:String, default:'en' },
        loop : { type:Number, default:0, validator: (v) => Number(v)===0||Number(v)===1 },
        rel : { type:Number, default:0, validator: (v) => Number(v)===0||Number(v)===1 },  
        listType: { type:String, required:false, validator: (v) => v==="search"||v==="user_uploades"||v==="playlist" },
        list: { type:String, required:false },  
        origin : { type:String, default:"http://localhost:8080" }
    },
    mounted() {
        window.YTConfig = {
            host: 'https://www.youtube.com/iframe_api'
        }
        let playerVars = {
            autoplay:this.autoplay, 
            hl:this.hl, 
            loop:this.loop, 
            listType:this.listType, 
            list:this.list, 
            origin:this.origin
        }
        this.player = YouTubePlayer(this.$refs.player, {
            host: "https://www.youtube.com",
            width: this.width,
            height: this.height,
            videoId: this.videoid,
            playerVars: playerVars
        })
        this.player.on('stateChange', (e) => {
            if (e.data === window.YT.PlayerState.ENDED) {
                this.$emit('ended')
            } else if (e.data === window.YT.PlayerState.PAUSED) {
                this.$emit('paused')
            } else if (e.data === window.YT.PlayerState.PLAYING) {
                this.$emit('played')
            }
        });
    },
    unmounted() {
        console.log("##unmount")
      this.player.destroy()
      delete this.player
    },
    watch : {
        videoid() {
            this.reload()
        },
        list() {
            this.reload()
        },
        listType() {
            this.reload()
        }
    },
    methods : {
        reload() {
            this.player.stopVideo()
            .then(() => {
                if (this.videoid) {
                    this.player.loadVideoById(this.videoid);
                    this.player.playVideo();
                } else if(this.listType === "playlist" && this.list.trim() !== "") {
                    this.player.loadPlaylist(this.list)
                    this.player.playVideo();
                }

            });
        }
    }
}
</script>

